@use 'global' as * ; 

.contact-textarea__text {
	-moz-appearance: none;
	-webkit-appearance: none;
	appearance: none;
	background-color: transparent;
	background-image: none;
	border: 1px solid $color-line;
	border-radius: 0;
	color: inherit;
	font-family: inherit;
	font-size: rem(14);
	height: rem(140);
	padding: rem(10) rem(16);
	width: 100%;

  @include mq(tab) {
    font-size: rem(16);
    height: rem(240);
  }
}

.contact-textarea__text:focus {
	border: 1px solid $color-main-dark2;
	box-shadow: none;
	outline: none;
}